import React, { Component, Suspense } from "react";
import { toast } from 'react-toastify';
import $ from 'jquery';
import { connect } from "react-redux";
import FileSaver from "file-saver";
import moment from 'moment'
import { Nav, Tab } from 'react-bootstrap';
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { Uppy } from "@uppy/core";
import { ugcActionRquest, getLastUgcRequest, getUgcTemplates, deleteUgcTemplate } from '../../../../../Services/Actions/ugcRightsActions'
import { loadAssetsDataWithDispatch } from '../../../../../Services/Actions/assetsActions'
import { getURLFromVideo, showAddAccountAlert, GET_NETWORK_ICON, isValidURL, SafariStatus } from '../../../../../Services/Utils/utils'
import { CLOUD, IS_LIVE } from '../../../../../Services/Api'
import { tinyRequest } from '../../../../../Services/Actions/tinyUrlAction'
import { getAccountList } from '../../../../../Services/Actions/connectionsActions'
//import FileInput from "@uppy/react/lib/FileInput";
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
