import { InnerLoader } from "../../../CommonComponents/InnerLoader";
import Swal from 'sweetalert2';

import { uppyKey, uppyBucket, uppyAwsKey, uppyAwsSecret, companionUrl } from '../../../../../Services/Utils/config'

const GetRightsGenralRequest = React.lazy(() => import('./GetRightsSteps/GetRightsGenralRequest'));
const GetRightsSocialAccount = React.lazy(() => import('./GetRightsSteps/GetRightsSocialAccount'));
const GetRightsCopyUrl = React.lazy(() => import('./GetRightsSteps/GetRightsCopyUrl'));
const GetRightsPendingAndExpire = React.lazy(() => import('./GetRightsStatus/GetRightsPendingAndExpire'));
const GetRightsDownload = React.lazy(() => import('./GetRightsStatus/GetRightsDownload'));
const GetRightsRejected = React.lazy(() => import('./GetRightsStatus/GetRightsRejected'));


const channel = SafariStatus ? window : new BroadcastChannel('connect');



class AssetsModalAside extends Component {
    constructor(props) {
        super(props)
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyBucket,
                            key: uppyAwsKey,
                            secret: uppyAwsSecret,
                            path: 'ugc-suite/tmp/${file.id}.${file.ext}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: companionUrl })

        this.uppyPostEdit = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['application/pdf', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyBucket,
                            key: uppyAwsKey,
                            secret: uppyAwsSecret,
                            path: 'ugc-suite/tmp/${file.id}.${file.ext}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: companionUrl })
        this.state = {
            step: 1,
            client_logo: null,
            client_logo_name: null,
            comment: '',
            lastComment: '',
            tncName: "",
            tncUrl: "",
            tnc: false,
            logoModal: false,
            fileModal: false,
            activeKey: 'ugc-socialAccount',
            connectedAccount: null,
            commentError: null,
            tncUrlError: null,
            tncError: null,
            requestedRespone: null,
            rightsVia: '',
            accountList: [], selectedAccount: null,
            hashValue: ``,
            requested: false,
            isRequesting: false,
            uploadType: "Upload",
            validUrl: true,
            validUrlError: false,
            templateList: [],
            filteredTemplate: [],
            selectedTemplate: {},
            templateName: '',
            tempAction: 0,
            tempId: 0,
            tempNameError: false,
            loading: true,
            hashError: false,
            accountError:false
        }
    }

    clickUploadType = (value) => event => {
        this.setState({
            uploadType: value, tncName: "",
            tncUrl: ""
        })
    }
    onClickToChange = (step) => event => {
        if (this.state.step == 1) {
            if (this.state.rightsVia && String(this.state.rightsVia).length > 0) {
                this.setState({
                    step, activeKey: "ugc-genralRequest", commentError: false, tempNameError: false, tncUrlError: false, tncError: false, validUrlError: false
                })
            }
            else {
                toast.error("Please select the mode for Right Request", { autoClose: 2000 })
            }
        }
        else if (this.state.step == 2) {
            if (this.state.rightsVia == "viaDM" || this.state.rightsVia == "linkBio") {
                if (this.state.comment && this.state.tncUrl && this.state.tnc && !this.state.tempNameError && this.state.templateName.trim() != "" && ((this.state.uploadType == "Url" && this.state.validUrl) || this.state.uploadType == "Upload")) {
                    // this.setState({ step, activeKey: "ugc-socialAccount" })
                    this.onClicktoSendRequest({ step, activeKey: "ugc-copyUrl" })
                }
                else {
                    if (!this.state.comment || String(this.state.comment).length == 0) this.setState({ commentError: true })
                    if (!this.state.tncUrl && this.state.uploadType == "Upload") this.setState({ tncUrlError: true })
                    if (!this.state.tnc) this.setState({ tncError: true })
                    if (!this.state.validUrl) this.setState({ validUrlError: true })
                    if (!this.state.tncUrl && this.state.uploadType == "Url") this.setState({ validUrlError: true })
                    if (this.state.templateName.trim() == "") this.setState({ tempNameError: true })
                }

            }
            else if (this.state.rightsVia == "viaComments") {
                const { selectedAccount } = this.state
                if (this.state.comment && this.state.comment.includes("#") && this.state.templateName) this.setState({ comment: this.state.comment }, () => this.setState({ step, activeKey: "ugc-copyUrl" }))
                else {
                    if (!this.state.comment || String(this.state.comment).length == 0 || !this.state.comment.includes("#")) this.setState({ commentError: true })
                    if (this.state.templateName.trim() == "") this.setState({ tempNameError: true })
                }
            }


        }
        else if (this.state.step == 3) {

        }

    }
    onClickToRemoveLogo = event => this.setState({
        client_logo: null, client_logo_name: null,
    })
    componentWillMount() {
        //this.onLoadLastRequestData()
        getUgcTemplates().then(response => {
            const { responseData } = response.data
            this.setState({ templateList: responseData.templates, loading: false })
        }).catch(error => {
            this.setState({ loading: false })
        })
    }

    componentDidMount() {
        this.uppyPostEdit.on('transloadit:result', (stepName, result) => {
            var imgpath = result.url.split('/');
            var imglength = imgpath.length;
            var filename = imgpath[imglength - 1];
            let { tncName, tncUrl } = this.state;
            tncUrl = `${result.id}.${result.ext}`
            tncName = result.name
            this.setState({ tncName, tncUrl, selectedFile: result.id, filename: `${CLOUD}tmp/${result.id}.${result.ext}`, selected: `${CLOUD}tmp/${result.id}.${result.ext}` }, () => {
                setTimeout(() => {
                    this.uppyPostEdit.cancelAll()
                    this.setState({ fileModal: false })
                }, 300)
            })

        })

        this.uppy.on('transloadit:result', (stepName, result) => {
            let { client_logo, client_logo_name } = this.state

            client_logo = `${CLOUD}tmp/${result.id}.${result.ext}`
            client_logo_name = result.name
            this.setState({ client_logo, client_logo_name, selectedlogoFile: result.id, logofilename: `${CLOUD}tmp/${result.id}.${result.ext}`, logoselected: `${CLOUD}tmp/${result.id}.${result.ext}` }, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.setState({ logoModal: false })
                }, 300)
            })

        })
        channel.addEventListener("message", this.readAuthenticationCode);

        this.onLoadAddAccount()
    }
    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId, type } = response;
        if (type == "social-account") {
            addUserAccount(networkId, data, () => {
                this.onLoadAddAccount()
            }, 0, 0);
        }
    }
    onChangeTncUrl = event => {
        this.setState({ tncName: "CustomUrlType", tncUrl: event.target.value, selectedFile: event.target.value, filename: event.target.value, selected: event.target.value, validUrl: isValidURL(String(event.target.value)), validUrlError: !isValidURL(String(event.target.value)) })
    }
    onLoadAddAccount = () => {
        const { accountList } = this.props
        if(accountList && accountList.length > 0){
            this.onFilterAccount(accountList)
        }
        else{
            getAccountList().then((response) => {
                const { responseData } = response.data
                this.props.setAccountList(responseData)
                this.onFilterAccount(accountList)
            }).catch(ex=>{})
        }
    }
    onFilterAccount = (data) =>{
        var self = this;
        const { modalAssetData } = self.props;
        if (data && data.length > 0) {
            const updateData = data.filter((item) => {
                if (item.Network.id == modalAssetData.networkId && !(item.ConnectedAccounts.api == 3 || item.ConnectedAccounts.api == 4)) {
                    if (String(item.Network.name).includes("Instagram")) return item
                    else return item.Network.id == modalAssetData.networkId
                }

            })
            var newData = []
            updateData.map((newItem) => {
                newItem.ConnectedAccounts.child.map((childItem) => {
                    newData.push({ ...newItem, userName: childItem.ConnectedAccounts.value2 })
                })
            })





            self.setState({ accountList: newData, selectedAccount: newData[0] }, () => {
                var { comment, selectedAccount, rightsVia } = this.state
                if (rightsVia == "viaComments") {
                    var matches = comment.match(/@([^@\s]+)/g);
                    if (selectedAccount) {
                        if (matches) {
                            var values = matches.map(match => match.replace('@', ''));
                            if (values && values.length > 0 && values.filter((item) => `@${item}` == matches[0]).length > 0) {

                                comment = comment.replace(`${matches[0]}`, `@${selectedAccount.userName}`)

                                this.setState({ comment })
                            }
                        } else {
                            if (comment && String(comment).includes("@")) {
                                comment = comment.replace(`${matches[0]}`, `@${selectedAccount.userName}`)

                                this.setState({ comment })

                            }
                        }
                    }

                }

            })
        }
    }
    onLoadLastRequestData = () => {
        var self = this;
        getLastUgcRequest().then((response) => {
            const { result } = response.data
            var client_logo = {}; var comment = {}; var tncURL = {}; var hashValue = []
            if (result.client_logo && String(result.client_logo).length > 0) {
                client_logo = { client_logo: result.client_logo }
            }
            if (result.custom_message && String(result.custom_message).length > 0) {
                comment = { comment: result.custom_message, lastComment: result.custom_message }
                hashValue = result.custom_message.match(/#([^#\s]+)/g)
            }
            if (result.custom_term_type && String(result.custom_term_type).length > 0) {
                tncURL = result.custom_term_type == "CustomUrlType" ? { tncName: result.custom_term_type, tncUrl: result.custom_terms, uploadType: "Url" } : { tncName: result.custom_term_type, tncUrl: result.custom_terms ? result.custom_terms.includes('ugc-suite/tmp') ? result.custom_terms : `https://cloud.taggbox.com/test/${result.custom_term_type}` : '' }

            }
            self.setState({ ...client_logo, ...comment, ...tncURL, hashValue:hashValue && hashValue.length > 0 ? hashValue[0].replace('#','') : this.state.hashValue })
        }).catch(ex=>{
            {}})
    }


    onClickClearFile = event => {
        this.setState({ tncName: "", tncUrl: "" })
    }
    onDeleteTemplate = (id) => {
        const { templateList, filteredTemplate } = this.state
        Swal.fire({
            title: 'Are you sure?',
            text: 'You want to delete this template!',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                deleteUgcTemplate(id).then((response) => {
                    const newList = templateList.filter(temp => temp.id != id)
                    const filterList = filteredTemplate.filter(temp => temp.id != id)
                    if (filterList.length > 0) {
                        this.setState({ filteredTemplate: filterList, selectedTemplate: filterList[0], comment: filterList[0].message, client_logo: filterList[0].client_logo, tncUrl: filterList[0].custom_terms, tncName: filterList[0].custom_term_type, tempId: filterList[0].id, tempAction: 0, templateName: filterList[0].name })
                    }
                    else this.setState({ filteredTemplate: [], selectedTemplate: {}, comment: '', client_logo: '', tncName: '', tncUrl: '', tempAction: 1, tempId: 0, templateName: '' })
                    this.setState({ templateList: newList })
                    toast.success('Template has been deleted.')
                }).catch(error => { toast.error("some error occured, please try again later.") })

            }
        })
    }

    componentWillUnmount() {
        this.uppy.close();
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    termsFileDownload = () => {
        //var blob = new Blob(["Hello, world!"], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(baseUrl('media/downloads/Terms&ConditionsforuseofUGC.docx'), "Terms&ConditionsforuseofUGC.docx");
    }
    modalClose = (step) => event => {
        if (step == 2) this.setState({ step: 1, activeKey: "ugc-socialAccount" })
        else this.props.modalClose(false)
    }
    onChangeComment = event => {
        const { rightsVia } = this.state
        if (rightsVia == 'viaComments') {
            const tag = event.target.value.match(/#([^#\s]+)/g);
            if (tag) this.setState({ hashValue: tag[0].replace('#', ''), hashError: false })
            else this.setState({ hashValue: '' })
        }
        this.setState({ comment: event.target.value })
    }
    onClicktnc = event => {
        const { tnc } = this.state
        this.setState({ tnc: !tnc, tncError: null })
    }

    onLogoClose = event => {
        this.uppy.cancelAll()
        this.setState({ logoModal: false })
    }
    onLogoOpen = event => this.setState({ logoModal: true }, () => {
        $(".uppy-Dashboard-close").click(() => {
            this.uppy.cancelAll()
            this.setState({ logoModal: false })
        })
    })

    onFileOpen = event => {
        this.setState({ fileModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppyPostEdit.cancelAll()
                this.setState({ fileModal: false })
            })
        })

    }

    onFileClose = () => {
        this.uppyPostEdit.cancelAll()
        this.setState({ fileModal: false })
    }
    onSelected = (key) => {
        this.setState({ activeKey: key })
    }

    sendNow = event => {
        const { modalAssetData, isBulkStatus, bulkRightsData } = this.props;
        const { comment, tncUrl, tncName, client_logo, connectedAccount, rightsVia, selectedAccount, hashValue, requested, tempAction, templateName, tempId, selectedTemplate } = this.state;

        let tempActionValue = tempAction == 1 ? tempAction : client_logo || selectedTemplate.message != comment || selectedTemplate.name != templateName || selectedTemplate.custom_terms != tncUrl || selectedTemplate.custom_term_type != tncName ? 2 : tempAction

        if ((!requested) && comment.length > 0 && hashValue.length > 0 && selectedAccount && Object.keys(selectedAccount).length > 0) {
            var requestedAccount = {}
            if (connectedAccount && connectedAccount.data.length > 0) {
                requestedAccount = connectedAccount.data[0]
            }
            var self = this;
            var assetsData = []
            if (isBulkStatus) {
                bulkRightsData.map((grItem) => {
                    assetsData.push({ "referenceId": grItem.referenceId, "username": grItem.username, "media": grItem.publishImage, "video": getURLFromVideo(grItem.mediaFile), ...requestedAccount, link: grItem.link, author: grItem.picture, postId: grItem.postId, "collection": grItem.collection })
                })

            }
            else {
                assetsData.push({ "referenceId": modalAssetData.referenceId, "username": modalAssetData.username, "media": modalAssetData.publishImage, "video": getURLFromVideo(modalAssetData.mediaFile), ...requestedAccount, link: modalAssetData.link, author: modalAssetData.picture, postId: modalAssetData.postId, "collection": modalAssetData.collection })
            }
            const requestedData = {
                "feedId": modalAssetData.feedId,
                "custom_message": comment,
                "networkId": modalAssetData.networkId,
                "brandlink": client_logo ? client_logo : "",
                "handle": selectedAccount && Object.keys(selectedAccount).length > 0 && selectedAccount.userName ? selectedAccount.userName : "",
                "hashtag": hashValue,
                "termslink": tncName == 'CustomUrlType' ? tncUrl : tncUrl.includes("cloud.tagbox.com") ? tncUrl : `${CLOUD}tmp/${tncUrl}`,
                "custom_term_type": "",
                "assets": assetsData,
                "rightsVia": rightsVia == 'viaComments' ? 1 : 0,
                "tempAction": tempActionValue,
                "tempId": tempId,
                "tempName": templateName
            }
            this.setState({ isRequesting: true })
            ugcActionRquest(requestedData).then((response) => {
                self.setState({ requested: true, isRequesting: false, accountError:false })
                self.props.loadAssetsDataWithDispatch()
                navigator.clipboard.writeText(comment);
                setTimeout(() => window.open(modalAssetData.link, '_blank'), 400)
            }).catch(ex => this.setState({ isRequesting: false, accountError:false }, () => toast.error("some error occured, please try again later.")))
        }
        else {
            if (hashValue.length == 0 || comment.length == 0) toast.error("Please fill all required fields.", { autoClose: 2000 })
            else if (!selectedAccount || Object.keys(selectedAccount).length <= 0) this.setState({accountError:true},()=>toast.error("Please connect your Social Account."))
            else toast.error("Already rights raise requested.", { autoClose: 2000 })
        }

    }
    onClicktoSendRequest = (updateStep) => {
        ///viaComments
        const { modalAssetData, isBulkStatus, bulkRightsData, userData } = this.props;
        const { comment, tncUrl, tncName, client_logo, connectedAccount, rightsVia, selectedAccount, hashValue, tempAction, tempId, templateName, selectedTemplate } = this.state;

        let tempActionValue = tempAction == 1 ? tempAction : client_logo != selectedTemplate.client_logo || selectedTemplate.message != comment || selectedTemplate.name != templateName || selectedTemplate.custom_terms != tncUrl || selectedTemplate.custom_term_type != tncName ? 2 : tempAction

        var requestedAccount = {}
        if (connectedAccount && connectedAccount.data.length > 0) {
            requestedAccount = connectedAccount.data[0]
        }
        var self = this;
        var assetsData = []
        if (isBulkStatus) {
            bulkRightsData.map((grItem) => {
                assetsData.push({ "referenceId": grItem.referenceId, "username": grItem.username, "media": grItem.publishImage, "video": getURLFromVideo(grItem.mediaFile), ...requestedAccount, link: grItem.link, author: grItem.picture, postId: grItem.postId, "collection": grItem.collection })
            })

        }
        else {
            assetsData.push({ "referenceId": modalAssetData.referenceId, "username": modalAssetData.username, "media": modalAssetData.publishImage, "video": getURLFromVideo(modalAssetData.mediaFile), ...requestedAccount, link: modalAssetData.link, author: modalAssetData.picture, postId: modalAssetData.postId, "collection": modalAssetData.collection ? modalAssetData.collection : '' })
        }
        const requestedData = {
            "feedId": modalAssetData.feedId,
            "custom_message": comment,
            "networkId": modalAssetData.networkId,
            "brandlink": client_logo ? client_logo : "",
            "handle": selectedAccount && Object.keys(selectedAccount).length > 0 && selectedAccount.userName ? selectedAccount.userName : "",
            "hashtag": hashValue,
            "termslink": tncName == 'CustomUrlType' ? tncUrl : tncUrl.includes("cloud.tagbox.com") ? tncUrl : `${CLOUD}tmp/${tncUrl}`,
            "custom_term_type": tncName,
            "assets": assetsData,
            "rightsVia": rightsVia == 'viaComments' ? 1 : rightsVia == "linkBio" ? 2 : 0,
            "tempId": tempId,
            "tempName": templateName,
            "tempAction": tempActionValue
        }

        this.setState({ isRequesting: true })
        ugcActionRquest(requestedData).then((response) => {
            self.props.loadAssetsDataWithDispatch()
            if (rightsVia == 'viaDM') {
                tinyRequest(modalAssetData.link).then((tResponse) => {
                    const { data } = tResponse.data
                    navigator.clipboard.writeText(comment + ' Post : ' + data.tiny_url + ' Form Link : ' + response.data.bitlylink);
                    // if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
                    // else setTimeout(() => window.open(data.tiny_url, '_blank'), 400)
                    self.setState({ requestedRespone: response.data, ...updateStep, isRequesting: false })
                }).catch((ex) => {
                    navigator.clipboard.writeText(comment + ' Post : ' + modalAssetData.link + ' Form Link : ' + response.data.bitlylink);
                    if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
                    else setTimeout(() => window.open(modalAssetData.link, '_blank'), 400)
                    self.setState({ requestedRespone: response.data, ...updateStep, isRequesting: false })

                })
            }
            else if (rightsVia == "linkBio") {
                navigator.clipboard.writeText(comment);
                self.setState({ requestedRespone: response.data, ...updateStep, isRequesting: false })
            }
        }).catch(error => this.setState({ isRequesting: false }, () => toast.error("Some error occured, please try again later.")))

    }
    onUpdateData = (provider, data) => {
        this.setState({ connectedAccount: { provider, data } })
    }
    rightsViaOption = event => {
        let filter = []
        const { templateList } = this.state
        if (event == "viaComments") {
            filter = templateList.filter(temp => temp.type == 1)
            if (filter.length > 0) {
                var matches = filter[0].message.match(/#([^#\s]+)/g);
                this.setState({ rightsVia: event, filteredTemplate: filter, selectedTemplate: filter[0], comment: filter[0].message, hashValue: matches.length > 0 ? matches[0].replace('#', '') : '', templateName: filter[0].name, tempAction: 0, tempId: filter[0].id })
            }
            else this.setState({ rightsVia: event, tempAction: 1, comment: '', tncName: '', tncUrl: '', client_logo: '', templateName: '', tempAction: 1, selectedTemplate: {}, tempId: 0, filteredTemplate: [] })
        }
        else if (event == "linkBio") {
            filter = templateList.filter(temp => temp.type == 3)
            if (filter.length > 0) {
                this.setState({ rightsVia: event, filteredTemplate: filter, selectedTemplate: filter[0], comment: filter[0].message, client_logo: filter[0].client_logo, tncName: filter[0].custom_term_type, tncUrl: filter[0].custom_terms, templateName: filter[0].name, tempAction: 0, tempId: filter[0].id, uploadType: filter[0].custom_term_type == "CustomUrlType" ? "Url" : "Upload" })
            }
            else this.setState({ rightsVia: event, tempAction: 1, comment: '', tncName: '', tncUrl: '', client_logo: '', templateName: '', tempAction: 1, selectedTemplate: {}, tempId: 0, filteredTemplate: [] })
        }
        else {
            filter = templateList.filter(temp => temp.type == 2)
            if (filter.length > 0) {
                this.setState({ rightsVia: event, filteredTemplate: filter, selectedTemplate: filter[0], comment: filter[0].message, client_logo: filter[0].client_logo, tncName: filter[0].custom_term_type, tncUrl: filter[0].custom_terms, templateName: filter[0].name, tempAction: 0, tempId: filter[0].id, uploadType: filter[0].custom_term_type == "CustomUrlType" ? "Url" : "Upload" })
            }
            else this.setState({ rightsVia: event, tempAction: 1, comment: '', tncName: '', tncUrl: '', client_logo: '', templateName: '', tempAction: 1, selectedTemplate: {}, tempId: 0, filteredTemplate: [] })
        }
    }
    onSelectTemplate = (event) => {
        const { templateList } = this.state
        const temp = templateList.filter(template => template.id == event.value)
        var matches = temp[0].message.match(/#([^#\s]+)/g)
        this.setState({ selectedTemplate: temp[0], comment: temp[0].message, client_logo: temp[0].client_logo, tncUrl: temp[0].custom_terms, tncName: temp[0].custom_term_type, templateName: temp[0].name, tempAction: 0, tempId: temp[0].id, tempNameError: false, hashValue: temp[0].type == 1 && matches.length > 0 ? matches[0].replace("#", '') : '', uploadType: temp[0].custom_term_type == "CustomUrlType" ? "Url" : "Upload" })
    }
    onCreateTemplate = (select) => {
        const { filteredTemplate, templateList } = this.state
        if (select && filteredTemplate.length > 0) {
            this.setState({ tempAction: 0, comment: filteredTemplate[0].message, tncUrl: filteredTemplate[0].custom_terms, tncName: filteredTemplate[0].custom_term_type, templateName: filteredTemplate[0].name, tempId: filteredTemplate[0].id, client_logo: filteredTemplate[0].client_logo, selectedTemplate: filteredTemplate[0] })
        }
        else {
            if (filteredTemplate.length < 5 && templateList.length < 10) {
                this.setState({ comment: '', tncName: '', tncUrl: '', client_logo: '', templateName: '', tempAction: 1, selectedTemplate: {}, tempId: 0 })
            }
            else {
                toast.error("Oops, you can't have more than 5 templates")
            }
        }
    }
    onTempNameChange = (event) => this.setState({ templateName: event.target.value, tempNameError: event.target.value.trim() == '' ? true : false })
    onChangeSelectedAccount = (item) => event => {
        let { comment, selectedAccount } = this.state
        var matches = comment.match(/@([^@\s]+)/g);
        if (item) {
            if (matches) {
                var values = matches.map(match => match.replace('@', ''));
                if (values && values.length > 0 && values.filter((item) => item == selectedAccount.userName).length > 0) {
                    comment = comment.replace(`@${selectedAccount.userName}`, `@${item.userName}`)
                }
            } else {
                if (comment && String(comment).includes("@")) {
                    comment = comment.replace(`@${selectedAccount.userName}`, `@${item.userName}`)
                }
            }
        }

        this.setState({ selectedAccount: item && Object.keys(item).length > 0 ? item : item, comment }, () => {
            const { selectedAccount } = this.state;
            const { modalAssetData } = this.props
            if (selectedAccount == null) {
                showAddAccountAlert({ id: modalAssetData.networkId, name: GET_NETWORK_ICON(modalAssetData.networkId) }, "social-account")
            }

        })
    }
    hashChange = event => {
        let { comment, hashValue } = this.state
        var matches = comment.match(/#([^#\s]+)/g);
        const hashTextValue = event.target.value
        if (matches) {
            var values = matches.map(match => match.replace('#', ''));
            if (values && values.length > 0 && values.filter((item) => item == hashValue).length > 0) {
                comment = comment.replace(`#${hashValue}`, `#${String(hashTextValue).trim()}`)
                this.setState({ hashValue: String(hashTextValue).trim(), comment: comment, hashError: false })
            }
        } else {
            if (comment && String(comment).includes("#")) {
                comment = comment.replace(`#${hashValue}`, `#${String(hashTextValue).trim()}`)
                this.setState({ hashValue: String(hashTextValue).trim(), comment: comment, hashError: false })
            }
            else this.setState({ hashError: true })
        }

    }
    render() {
        const { modalAssetData, userData } = this.props
        const { step, activeKey, connectedAccount, requestedRespone, comment, rightsVia, accountList, selectedAccount, hashValue, isRequesting, templateList, selectedTemplate, loading, hashError, accountError } = this.state
        return modalAssetData && Object.keys(modalAssetData).length > 0 ? <div className="rights_modal_sidebar position-relative" style={step == 3 ? { paddingBottom: 0 } : null}>
            {loading ? <InnerLoader /> : <div className="rights_modal_aside_wrap">
                {modalAssetData.ugcRights == null || modalAssetData.ugcRights.status == 4 ? <Tab.Container activeKey={activeKey} onSelect={this.onSelected}>
                    <div className="ugc-wizard-nav">
                        <Nav variant="nav" className="bg-light justify-content-between row gx-0">
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 1 || step == 2 || step == 3 ? `active` : ``}`} eventKey="ugc-socialAccount" onClick={event => this.setState({ step: 1 })}>
                                    <span className="d-flex me-1 text-success">
                                        <i className="ri-checkbox-circle-fill icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Choose Method</span>

                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 2 || step == 3 ? `active` : `disabled`}`} eventKey="ugc-genralRequest">
                                    <span className="d-flex me-1">
                                        <i className="checkIcoFilled ri-checkbox-circle-fill icon-5 text-success"></i>
                                        <i className="checkIco ri-checkbox-circle-line icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Generate Request</span>
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 3 ? `active` : `disabled`}`} eventKey="ugc-copyUrl">
                                    <span className="d-flex me-1">
                                        <i className="checkIcoFilled ri-checkbox-circle-fill icon-5 text-success"></i>
                                        <i className="checkIco ri-checkbox-circle-line icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Preview</span>
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </div>
                    <Tab.Content>

                        <Tab.Pane eventKey="ugc-socialAccount">
                            <Suspense fallback={<></>}>
                                <GetRightsSocialAccount modalAssetData={modalAssetData} onUpdateData={this.onUpdateData} connectedAccount={connectedAccount} rightsViaOption={this.rightsViaOption} rightsVia={rightsVia} />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-genralRequest">
                            <Suspense fallback={<></>}>
                                <GetRightsGenralRequest state={this.state} onChangeComment={this.onChangeComment} onClicktnc={this.onClicktnc} onClickClearFile={this.onClickClearFile} uppyPostEdit={this.uppyPostEdit} uppy={this.uppy} onLogoClose={this.onLogoClose} onLogoOpen={this.onLogoOpen} onClickToRemoveLogo={this.onClickToRemoveLogo} onFileOpen={this.onFileOpen} onFileClose={this.onFileClose} rightsVia={rightsVia} onChangeTncUrl={this.onChangeTncUrl} clickUploadType={this.clickUploadType} modalAssetData={modalAssetData} onSelectTemplate={this.onSelectTemplate} onTempNameChange={this.onTempNameChange} onCreateTemplate={this.onCreateTemplate} onDeleteTemplate={this.onDeleteTemplate} />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-copyUrl">
                            <Suspense fallback={<></>}>
                                <GetRightsCopyUrl key={activeKey} modalAssetData={modalAssetData} requestedRespone={requestedRespone} comment={comment} rightsVia={rightsVia} accountList={accountList} selectedAccount={selectedAccount} onChangeSelectedAccount={this.onChangeSelectedAccount} hashValue={hashValue} hashChange={this.hashChange} sendNow={this.sendNow} isRequesting={isRequesting} onChangeComment={this.onChangeComment} hashError={hashError} bioLink={userData.bioLink} accountError={accountError}/>
                            </Suspense>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container>
                    :
                    modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 1 ?
                        <Suspense fallback={<></>}>
                            <GetRightsPendingAndExpire days={parseInt(30) - (parseInt((parseInt((moment().diff(moment(modalAssetData.ugcRights.created), 'days'))))) + parseInt(1))} expireStatus={true} modalAssetData={modalAssetData} />
                        </Suspense>
                        : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 2 ?
                            <Suspense fallback={<></>}>
                                <GetRightsDownload modalAssetData={modalAssetData} />
                            </Suspense> : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 3 ? <Suspense fallback={<></>}>
                                <GetRightsRejected modalAssetData={modalAssetData} onUpdateRejectedRightDetail={this.props.onUpdateRejectedRightDetail} />
                            </Suspense> : null}
            </div>}
            {(modalAssetData.ugcRights == null || modalAssetData.ugcRights.status == 4) && step != 3 ? <div className="rights_modal_actions text-center modal-footer w-100">
                {step == 1 || step == 2 ? <><button type="button" className="btn btn-secondary me-2" onClick={this.modalClose(step)}>{step == 1 ? `Cancel` : `Back`}</button>
                    <button type="button" className={`btn btn-primary ${isRequesting ? `spinner disabled` : ``}`} onClick={this.onClickToChange(step == 1 ? 2 : step == 2 ? 3 : 4)}>Next Step</button>
                </> : null}
                {/* {step == 3 ? <button type="button" className="btn btn-secondary me-2" onClick={this.modalClose(step)}>Done</button> : null} */}
            </div> : null}
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return {
        accountList: state.addFeed.allConnectedAcc,
        userData: state.authenticated.userData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        loadAssetsDataWithDispatch: () => dispatch(loadAssetsDataWithDispatch()),
        setAccountList: data => dispatch({type:"GET_ALL_CONNECTED_ACCOUNTS",payload:data})
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AssetsModalAside);